@import url(https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap);
.header{width:100%;background-color:#afabab;display:flex;justify-content:center;align-items:center;height:8vh}.header__item{padding:5px 10px;display:block;border:1px solid blanchedalmond;font-size:30px;line-height:45px;text-align:center;font-family:"Roboto" ,sans-serif;transition:all 0.2s linear}.header__item:hover{box-shadow:0 0 5px blanchedalmond;-webkit-transform:scale(1.01);transform:scale(1.01);transition:all 0.1s linear}.header__item:active{box-shadow:0 0 10px blanchedalmond;-webkit-transform:scale(0.99);transform:scale(0.99);transition:all 0.1s linear}.active{background-color:blanchedalmond}

.lds-ring{display:inline-block;position:relative;width:200px;height:200px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #3eeb09;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:blanchedalmond transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loader{position:fixed;width:100vw;height:100vh;display:flex;align-items:center;justify-content:center}

.noteForm{width:60%;margin:0 auto;border:3px solid black;border-radius:10px;margin-top:40px;padding:20px 40px;font-family:"Roboto", sans-serif;position:relative}.noteForm__wrapper{display:flex;flex-direction:column;align-items:center;margin-top:20px}.noteForm__titleInput{width:60%;height:40px;font-size:30px;text-align:center}.noteForm__descriptionInput{width:80%;min-height:300px;height:100%;margin-top:10px;font-size:20px;padding-left:30px;padding-top:10px}.noteForm__tagsInput{margin-top:10px;margin-bottom:40px;max-width:580px;word-wrap:break-word}.noteForm__saveBtn{width:80px;height:30px;position:absolute;right:100px;bottom:20px;cursor:pointer}

.noteDetail{width:70%;margin:0 auto;border:2px solid blanchedalmond;border-radius:5px;margin-top:20px;padding:35px 40px;display:flex;flex-direction:column;align-items:center;position:relative}.noteDetail__edit{position:absolute;right:60px;top:30px;width:50px;height:50px;border-radius:50%;cursor:pointer}.noteDetail__importance{position:absolute;top:7px;left:20px;width:50px}.noteDetail__title{height:44px;font-size:30px;text-align:center;font-weight:600;padding:5px;word-wrap:break-word}.noteDetail__description{margin-top:30px;width:100%;font-size:17px;font-weight:400;margin-bottom:60px;word-wrap:break-word;min-height:150px;height:100%;padding:7px;padding-left:10px}.noteDetail .textArea{min-height:150px;height:100%;padding:7px;padding-left:10px}.noteDetail__date{position:absolute;right:10px;bottom:0px;color:#6e6c6c;font-size:14px}.noteDetail__tags{position:absolute;left:20px;bottom:15px;width:530px;font-size:23px}.noteDetail__closeBtn{position:absolute;top:10px;right:10px;font-size:19px;background-color:transparent;border:1px solid blanchedalmond;border-radius:6px}.noteDetail .marckSpan{color:#529bfa}

.note{width:40%;margin-top:30px;position:relative;cursor:pointer}.note__wrapper{padding:15px 30px;transition:all 0.1s linear;box-shadow:2px 2px 7px rgba(0,0,0,0.4);border:3px solid blanchedalmond;border-radius:15px;position:relative}.note__wrapper:hover{box-shadow:5px 5px 10px rgba(0,0,0,0.6);-webkit-transform:scale(1.01);transform:scale(1.01);transition:all 0.1s linear}.note__wrapper:active{box-shadow:3px 3px 20px rgba(0,0,0,0.5);-webkit-transform:scale(0.99);transform:scale(0.99);transition:all 0.1s linear}.note__title{text-align:center;word-wrap:break-word}.note__description{margin-top:7px;height:80px;width:80%;word-wrap:break-word;overflow:scroll}.note__description::-webkit-scrollbar{width:0}.note__tags{font-size:11px;color:gray}.note__date{position:absolute;right:10px;bottom:10px}.note__deleteBtn{color:red;position:absolute;left:-60px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:50px;background-color:transparent;font-weight:500}

.searchBar{width:100%;margin:0 auto;display:flex;flex-direction:column;align-items:center;background-color:#afabab;padding:20px 10px;border:1px solid blanchedalmond}.searchBar__wrapper{position:relative;width:80%}.searchBar__btn{width:120px;font-size:20px;font-weight:500;margin-top:10px;cursor:pointer}.searchBar__icon{position:absolute;height:28px;width:28px;background-position:center;background-repeat:no-repeat;background-size:contain;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:10px}.searchBar__input{width:100%;height:40px;border-radius:10px;padding-left:50px;font-size:20px;color:black}.searchBar__option{width:70%;margin:0 auto;margin-top:20px;display:flex;justify-content:space-between}.searchBar__option_input{width:250px;height:40px;font-size:20px;padding-left:20px}.searchBar__option_showBy{width:80px;height:40px;font-size:20px;padding-left:20px}

.notesPage__wrapper{display:flex;flex-direction:column;align-items:center;background-color:#d8d8d8}

*,*::before,*::after{padding:0;margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif}li{list-style:none}a{text-decoration:none;color:black}.page{position:absolute;width:100%;margin:0 auto;background-color:#d8d8d8;min-height:92vh;height:100%}.page-enter-active{-webkit-animation:fade-in 1s forwards;animation:fade-in 1s forwards}.page-exit-active{-webkit-animation:fade-out 1s forwards;animation:fade-out 1s forwards}@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}@keyframes fade-in{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fade-out{0%{opacity:1}100%{opacity:0}}@keyframes fade-out{0%{opacity:1}100%{opacity:0}}.pageWrapper{position:absolute;width:100%}

